.App{
  background-color: #121212;
  height: 100vh;
}
.header,.account,.total-rockets{
  color: #ffffff;
}
.alert{
  color: red;
}
.message{
  margin: 1em 0em;
}
.header{
  font-size: 3rem;
}
.main{
  min-height: 45vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.transactions{
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  color: #fff;
  height: 55vh;
  overflow: auto;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.transactions::-webkit-scrollbar {
  display: none;
}
.one-rocket{
  margin: 0.5em 0em;
  background: #333;
  padding: 1em 0.5em;
}